import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
import wPicker from '@/components/w-picker/w-picker.vue';
export default {
	components: {
		wPicker
	},
	data() {
		return {
			is_child: '',
			is_miniBind: '',
			imgRemote: imgRemote,
			load: true,
			navBar: navBar,
			tabList: [{
				name: '待审核'
			}, {
				name: '备货中'
			}, {
				name: '配送中'
			}, {
				name: '已收货'
			}, {
				name: '已取消'
			}],
			activeTab: 6,
			showOrderType: false,
			childListProps: {
				"label": "nickname",
				"value": "zid"
			},
			childList: [{
				"zid": "",
				"nickname": "当前账号",
			}],
			childzid: '',
			childtxt: '当前账号',
			isActive: '全部',
			orderTitle: '全部订单',
			type: 1,
			search_default: false,
			is_look: '',
			orderList: [],
			orderInfo: '',
			page: 1,
			map: false,
		};
	},
	methods: {
		/**
		 * 获取子账号信息
		 */
		childInfo() {
			var that = this;
			that.childList = [{
				"zid": "",
				"nickname": "当前账号",
			}]
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("childInfo", data, (res) => {
				if (res.data.code == 200) {
					var childInfo = res.data.data;
					var arr = ["当前账号"];
					var num = [""];
					for (var i = 0; i < childInfo.length; i++) {
						that.childList.push(childInfo[i])
					}
				}
			})
		},
		//初始订单请求
		orderListb() {
			var that = this;
			that.search_default = false;
			var page = 1;
			var type = that.type;
			if (that.isActive != "全部") {
				var status = that.isActive + 1;
			} else {
				var status = '';
			}
			var num = 10;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				type: type
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				num: num,
				sign: sign,
				type: type,
				status: status,
			}
			rs.getRequest("orderList", data, (res) => {
				if (res.data.code == 200) {
					if (res.data.data.list.length > 0) {
						if (res.data.data.list.length <= 9) {
							that.load = false;
						} else {
							that.load = true;
						}
						that.is_look = res.data.data.is_look;
						that.orderInfo = res.data.data;
						that.orderList = res.data.data.list;
						// console.log(res.data.orderInfo)
					} else {
						that.search_default = true;
						that.load = '空';
					}
				}
			})
		},
		orderLista() {
			var that = this;
			that.orderList = [];
			that.search_default = false;
			var select_zid = that.childzid;
			if (that.isActive != "全部") {
				var status = that.isActive + 1;
			} else {
				var status = '';
			}
			var page = 1;
			var type = that.type;
			var num = 10
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				type: type
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				num: num,
				sign: sign,
				type: type,
				select_zid: select_zid,
				status: status,
			}
			rs.getRequest("orderList", data, (res) => {
				if (res.data.code == 200) {
					if (res.data.data.list.length > 0) {
						if (res.data.data.list.length <= 9) {
							that.load = false;
						} else {
							that.load = true;
						}
						that.is_look = res.data.data.is_look;
						that.orderInfo = res.data.data;
						that.orderList = res.data.data.list;
					} else {
						that.search_default = true;
						that.load = '空';
					}
				}
			})
		},
		onChild(e) {
			this.childtxt = e.obj.nickname;
			this.childzid = e.obj.zid;
			this.orderLista();
		},
		selectAccount() {
			this.$refs.account.show();
		},
		titleTab(data) {
			this.orderTitle = data;
			this.showOrderType = false;
			if (data == "未支付") {
				this.type = 2;
			} else if (data == "全部订单") {
				this.type = 1;
			}
			this.orderLista();
		},
		changeFirst(index) {
			this.isActive = index;
			this.orderLista();
		},
		rightClick() {
			this.showOrderType = true;
		},
		orderDetailPage() {
			uni.navigateTo({
				url: '/pages/order/orderdetail'
			})
		},
		// //回到顶部
		goTop() { // 一键回到顶部
			uni.pageScrollTo({
				scrollTop: 0
			})
		},
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow() {
		var that = this;
		that.is_child = uni.getStorageSync("is_child");
		that.is_miniBind = uni.getStorageSync("is_miniBind");
		that.childList = [{
			"zid": "",
			"nickname": "当前账号",
		}]
		if (!uni.getStorageSync("cdj_token")) {
			uni.navigateTo({
				url: '/pages/account/login'
			});
		} else {
			let aShow = app.aData.show;
			// console.log(aShow)
			if (aShow == false) {
				that.orderListb() //订单列表
				that.goTop()
				that.orderList = [];
				that.childtxt = '当前账号';
				that.childzid = '';
				that.type = 1;
				that.isActive = '全部';
			}
			if (uni.getStorageSync("is_child") == 0) {
				that.childInfo();
				//用户信息
			}
		}
	},
	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {
		if (this.map == true) {
			app.aData.show = true;
		} else {
			app.aData.show = false;
		}
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {
		//订单列表流加载
		var that = this;
	
		if (that.isActive != "全部") {
			var status = that.isActive + 1;
		} else {
			var status = '';
		}
		var page = that.page;
		var type = that.type;
		var num = 10
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var obj = {
			appid: appid,
			page: page + 1,
			timeStamp: timeStamp,
			type: type
		}
		var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		var data = {
			appid: appid,
			page: page + 1,
			timeStamp: timeStamp,
			num: num,
			sign: sign,
			type: type,
			status: status
		}
		if (uni.getStorageSync("is_child") == 0) {
			data.select_zid = that.childzid;
		}
		rs.getRequest("orderList", data, (res) => {
			if (res.data.code == 200) {
				if (res.data.data.list != '') {
	
					for (var i = 0; i < res.data.data.list.length; i++) {
						that.orderList.push(res.data.data.list[i])
					}
					that.is_look = res.data.data.is_look;
					that.page = page + 1;
				} else {
					that.load = false;
	
				}
			}
		})
	},
	onLoad() {
		app.aData.show = false;
		uni.hideTabBar();
	}
};
